/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
    margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role="list"],
ol[role="list"] {
    list-style: none;
}

/* Set core root defaults */
html:focus-within {
    scroll-behavior: smooth;
}

/* Set core body defaults */
body {
    min-height: 100vh;
    overflow-y: hidden;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    -webkit-text-decoration-skip: ink;
            text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
    max-width: 100%;
    display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
    font: inherit;
}

button {
    background: none;
    border: 1px solid rgb(187, 187, 187);
    border-radius: 2px;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        -webkit-animation-duration: 0.01ms !important;
                animation-duration: 0.01ms !important;
        -webkit-animation-iteration-count: 1 !important;
                animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

#root {
    min-height: inherit;
}
/* Builder, Builder sections */

.builder {
    display: grid;
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 1fr auto;
    height: 100svh;
    min-height: 100svh;
}

.builder .navbar {
    grid-column: 1 / 3;
    display: flex;
    justify-content: space-between;
    padding: 0.7rem;
    background: #d9d9d9;
}

.builder .navbar .course {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.builder .navbar .course .course-image {
    width: 50px;
    height: 30px;
    background: #000;
    margin-right: 0.5rem;
}

.builder .navbar .options {
    display: flex;
    align-items: center;
}

.builder .navbar .options .option {
    margin-right: 0.7rem;
}

.builder .navbar .options .option:last-child {
    margin-right: 0;
}

.builder .navbar .options .option.preview {
    background: #000;
    color: #fff;
    padding: 0.5rem 2rem;
    border-radius: 2rem;
}

.builder .lessons {
    grid-column: 1 / 3;
}

.builder .lesson-content {
    background-color: #f8f8f8;
    overflow: auto;
    padding: 1rem 2rem 5rem 2rem;
}

.builder .grid-wrapper {
    min-height: 100%;
    margin: 0 auto;
}

.builder .grid {
    display: grid;
    grid-template-columns: 1fr;
    margin: 0 auto;
    -webkit-transform-origin: top;
            transform-origin: top;
    /* gap: 24px; */
}

.builder .grid .preview {
    height: 58px;
    background: #dde6ef;
}

.builder .grid .grid-row {
    display: flex;
    grid-gap: 24px;
    gap: 24px;
}

.builder .grid .grid-column {
    flex-basis: 100%;
    border: 1px solid rgba(0, 0, 0, 0.00);
}

@media screen and (min-width: 0px) {

    .grid,
    .grid-wrapper {
        max-width: 480px;
    }
}

@media screen and (min-width: 600px) {

    .grid,
    .grid-wrapper {
        max-width: 960px;
    }
}

@media screen and (min-width: 1024px) {

    .grid,
    .grid-wrapper {
        max-width: 1280px;
    }
}

.builder .sidebar {
    min-width: 15rem;
    background: #343536;
    padding: 1rem;
}

.builder .lesson-content .topbar {
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
}

.builder-elements-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.builder-elements-menu .element-wrapper {
    margin-bottom: 0.6rem;
}

.builder-elements-menu .element {
    border: none;
    background: #d7d7d7;
    color: #232323;
    width: 100%;
    height: 2.8rem;
    font-size: 1rem;
}

@media screen and (max-width: 800px) {
    .builder {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto 1fr auto;
    }

    .main {
        order: -1;
    }

    .builder-elements-menu {
        display: inline-grid;
        grid-auto-flow: column;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .builder-elements-menu .element {
        margin-bottom: 0;
        padding: 0 1rem;
    }

    .builder .lesson-content {
        grid-column: 1 / 3;
    }
}

/*
*
* ==========================================
* FLIP CARD WITH TEXT
* ==========================================
*
*/

/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-card {
    background-color: transparent;
    width: 300px;
    height: 300px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

/* This container is needed to position the front and back side */
.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

/* Do a horizontal flip when you move the mouse over the flip box container */
.flip-card:hover .flip-card-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
    background-color: #bbb;
    color: black;
}

/* Style the back side */
.flip-card-back {
    background-color: #2980b9;
    color: white;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    overflow: auto;
}

.image-component {
    display: flex;
}

#transform-scale {
    display: flex;
    align-items: center;
    width: 200px;
    border: 1px solid #d1d1d1;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 15px;
    background: #343536;
    color: white;

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}

.undefined-element {
    background: #ffc107;
    color: #343a40;
    font-weight: bold;
    text-align: center;
    padding: 1rem;
    border-style: dashed;
    border-width: 3px;
}

.undefined-element .message {
    margin: 0 0.5rem;
}

#placement-preview {
    position: absolute;
}

#placement-preview .overlay {
    position: absolute;
    background: #cae4ff;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

@-webkit-keyframes slide-down {
    from {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    to {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes slide-down {
    from {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    to {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

.slide-down {
    -webkit-animation: slide-down 300ms;
            animation: slide-down 300ms;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.fade-in {
    -webkit-animation: fade-in 200ms, slide-down 300ms;
            animation: fade-in 200ms, slide-down 300ms;
}

.builder .drag-handle {
    display: flex;
    position: absolute;
    right: 0px;
    top: 50%;
    -webkit-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
    background: #343536;
    color: #d7d7d7;
    z-index: 1;
    height: 25;
    width: 25;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    cursor: -webkit-grab;
    cursor: grab;
    padding: 0.4rem;
}

.builder .drag-handle-visible {
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.05);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 3px;
}

.builder .grid-column.dragging {
    opacity: 0.5;
}

.builder .drag-overlay {
    overflow: hidden;
    background: #f8f8f8;
    opacity: .8;
}
